// Your variable overrides

//Colors

$white:    #fff ;
$gray-000: #f8f9fa ;
$gray-100: #f1f3f5 ;
$gray-200: #e9ecef ;
$gray-300: #dee2e6 ;
$gray-400: #ced4da ;
$gray-500: #adb5bd ;
$gray-600: #868e96 ;
$gray-700: #495057 ;
$gray-800: #343a40 ;
$gray-900: #212529 ;
$black:    #000 ;

$grays: (
  "000": $gray-000
);


$red:     #c92a2a ;
$pink:    #a61e4d ;
$purple:  #862e9c ;
$violet:  #5f3dc4 ;
$indigo:  #364fc7 ;
$blue:    #1864ab ;
$cyan:    #0b7285 ;
$teal:    #087f5b ;
$green:   #2b8a3e ;
$lime:    #5c940d ;
$yellow:  #e67700 ;
$orange:  #d9480f ;
$violet5:  #845ef7 ;

$theme-color-interval:      10%;

$declain:    $violet5;
$accept:     $blue;
$base:       $gray-600;

$theme-colors: (
  "declain": $declain,
  "accept": $accept,
  "base": $base
  );

//Fonts
$font-family-sans-serif:       "Noto Sans", "DejaVu Sans", "Segoe UI", Roboto, -apple-system, BlinkMacSystemFont, "Oxygen", "Ubuntu", "Cantarell", Arial, "Helvetica Neue", "Helvetica", sans-serif ;
$font-family-monospace:       "Source Code Pro",  Menlo, Consolas, "Liberation Mono",  monospace ;
$font-size-base:              16px ;
$h5-font-size:                $font-size-base * 1.375;
$h4-font-size:                $font-size-base * 1.625;
$h3-font-size:                $font-size-base * 2;
$h2-font-size:                $font-size-base * 3;
$h1-font-size:                $font-size-base * 4;
$h7-font-size:                $font-size-base * 0,875;
$h8-font-size:                $font-size-base * 0,75;

//Tables
$table-cell-padding:          .3;

//Navs

$nav-link-padding-y:                8px;

//Navbar

$navbar-nav-link-padding-x:         8px;

//Forms

$custom-control-indicator-size:         16px;
$custom-control-gutter:                 8px;
$custom-control-indicator-size:         16px;

//Alerts

//$alert-padding-y:                   .75rem !default;
$alert-padding-x:                       20px;
//$alert-margin-bottom:               1rem !default;

//Grid columns
$grid-gutter-width:           24px;

//links
$link-decoration:                         underline;

